import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { filterPostDataAppendWebFilter } from '../../../actions/filterpostAction'
import "./scss/Compact.scss"
import AverageRating from "../../Themes/Elements/AverageRating/AverageRating";
import { isTagShop } from "../../../actions/api";
import { onSitePopup } from "../../../actions/OtherActions";

const getBarWidth = (count, total) => {
       try {
              return ((count / total) * 100).toFixed(2);
       }
       catch (e) {
              return 0;
       }
}

class Compact extends PureComponent {

       render() {
              const {  wall } = this.props;
              const { Reviews, SnapUp } = wall
              const { button, title, reviewDetails, star, reviewList } = Reviews
              return (
                     <div className="tb_c_container">
                            <div className={`tb_c_title tb-cTBfont-${title?.style?.variant}`}>{title.text}</div>
                            <div className="tb_c_wrapper">
                                   <div className="tb_c_rating">
                                          <div className="tb_c_rating_wrap">
                                                 <div className="tb_c_avg">
                                                        <div className="tb_c_rview_star">
                                                               <AverageRating size={20} rating={parseFloat(reviewDetails?.avgRating)} color={star?.color} />
                                                        </div>
                                                        <div className={`tb_c_review_total_post`}>
                                                               {reviewDetails?.avgRating} out of 5
                                                        </div>
                                                 </div>
                                                 <div className="tb_c_total">Based on {reviewDetails?.totalReviewGiven} reviews</div>
                                          </div>
                                   </div>
                                   <div className="tb_c_avg_st">
                                          {
                                                 reviewList.map((item, index) => <div className="tb_c_avg_list" key={`rating_${index}`}>
                                                        <AverageRating size={16} rating={item.rating} color={star?.color} />
                                                        <div className="tb_c_bar">
                                                               <div className="tb_c_bar_active" style={{ width: `${getBarWidth(item.count, reviewDetails?.totalReviewGiven)}%` }}><div></div></div>
                                                        </div>
                                                        <div className="tb_c_rate_count">
                                                               {item.count}
                                                        </div>
                                                 </div>)
                                          }
                                   </div>
                                   {
                                          SnapUp.Form.status && isTagShop ? <div className="tb_c_banner_btn_wrap" ref={this.dropdownRef}>
                                                 <div className={`tb_c_banner_btn tb-cTBfont-${button.style.variant}`} onClick={() => this.props.onSitePopup(true)}>{button.text}</div>
                                          </div> : null
                                   }

                            </div>
                     </div>
              );
       }
}

const mapDispatchToProps = dispatch => {
       return {
              filterPostDataAppendWebFilter: (wallID, timeStamp, postCount, networkId, after, heightEvent, tagName) => dispatch(filterPostDataAppendWebFilter(wallID, timeStamp, postCount, networkId, after, heightEvent, tagName)),
              onSitePopup: (status) => dispatch(onSitePopup(status)),
       }
}
export default connect("", mapDispatchToProps)(Compact);